.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;   
}

.App-header {
  background-color: #222;
  height: auto;
  padding-top: 25px;
  color: white;
  font-size: 1.5em;
  padding-bottom: 30px; 
}

.App-intro {
  font-size: large;
  display: inline;
  margin-right: 5px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.titleField {
  width: 200px;
  height: 15px;
  margin-top: 20px;
  padding: 5px;
}
.customField {
  width: 100px;
  height: 15px;
  margin-left: 20px;
  padding: 5px;
}

#logoSpinDiv {
  margin: 25px;
}

.spinField {
  width: 60px;
  height: 15px;
  margin: 20px;
  padding: 5px;
}

select {
  padding: 5px;  
  display: inline;
}

.colorButton {
  height: 40px;
  width: 40px;
  margin-right: 5px;
  -webkit-box-shadow: 2px 2px 5px darkgray;
          box-shadow: 2px 2px 5px darkgray;
  border: none;
  vertical-align: middle; 
}

.active {
  -webkit-transform: scale(0.95);
      -ms-transform: scale(0.95);
          transform: scale(0.95);
  outline: 2px solid lightblue;    
}

@media (min-width: 600px) {
  #colorButtons{
    height: 70px;
    line-height: 70px;
    margin-bottom: 20px;
  }

  #bgButtons{
    height: 70px;
    line-height: 70px;  
  }
}

@media (max-width: 600px) {
  #colorButtons{
    height: 120px;
    line-height: 60px;
    margin-bottom: 20px;
  }

  #bgButtons{
    height: 120px;
    line-height: 60px; 
  }
}



p {
  margin: 0;
  padding: 0;
  height: 34px;  
}

#greenButton {
  background-color: green;
}

#redButton {
  background-color: red;
}

#whiteButton {
  background-color: white;
}

#blueButton {
  background-color: blue;
}

#blackButton {
  background-color: black;
}

button:focus {outline:1;}






